import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col, Form, Divider, Card, message } from 'antd';
import { isEmpty, cloneDeep } from 'lodash';
import { CartHeadAction } from 'components/common';
import { Btn, BtnSave, BtnExport } from 'components/common/button';
import { createGradePartGrading } from 'services/gradePartGrading';
import { createClassGradePartGrading, lockOrUnlockClassGradePartGrading, exportClassGradePartGrading } from 'services/classGradePartGrading';
import TableEnteringProcessScore from './TableEnteringProcessScore';
import FormInfoCommon from './FormInfoCommon';
import GradePartPopup from './PopupGradePart';

@Form.create({ name: 'EnteringProcessScorePage' })
class EnteringProcessScorePage extends Component {
  state = {
    visible: false,
    selectedGradeParts: [],
    reload: 1,
    tableData: [],
    paramSearch: {},
    savingEntities: {},
    selectedEntities: [],
    isDisableActionBtn: true,
    isDisableExportBtn: true,
    loadingSaving: false,
    loadingLocking: false,
    loadingExporting: false
  };

  setSelectedGradeParts = selectedGradeParts => {
    this.setState({ selectedGradeParts })
  }

  handleExportExcel = async() => {
    try {
      const { paramSearch: { classId, programDetailId } } = this.state;
      this.setState({ loadingExporting: true });
      await exportClassGradePartGrading({ classId, programDetailId })
    } catch (err) {
      message.error('C li xy ra');
    }
    this.setState({ loadingExporting: false });
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  handleSearch = async params => {
    console.log(params, this.state.selectedGradeParts);
    this.setState({ paramSearch: {...params, ...{ gradeParts: this.state.selectedGradeParts }}});
    // try {
    //   const res = await getClassGradePartGradings(params);
    //   if(res.data) {
    //     console.log(res.data);
    //     this.setState({ tableData: res.data.items });
    //   }
    // } catch (err) {
    //   message.error('C li xy ra');
    // }
  }

  handleSave = async() => {
    try {
      const { programDetailId } = this.state.paramSearch;
      this.setState({ loadingSaving: true });
      const grades = cloneDeep(this.state.savingEntities);

      grades.forEach(grade => {
        grade.programDetailId = programDetailId;
        if(!isEmpty(grade.gradeParts)) {
          grade.gradeParts.forEach(gradePart => {
            gradePart.grade = grade[`gradePart_${gradePart.gradePartId}`]
          })
        }
      })
      console.log(grades, createGradePartGrading, createClassGradePartGrading);
      await createClassGradePartGrading(grades);
      message.success('Lu thnh cng');
      // this.handleSearch({ id: this.state.paramSearch.id });
    } catch (err) {
      message.error('C li xy ra');
    }
    this.setState({ loadingSaving: false });
  }

  lockGradeParts = async() => {
    const { selectedEntities, paramSearch: { programDetailId } } = this.state;
    this.setState({ loadingLocking: true });
    const requestData = selectedEntities.map(entity => ({
      classId: entity.classId,
      studentId: entity.studentId,
      programDetailId,
      isLocked: true
    }));
    try {
      await lockOrUnlockClassGradePartGrading(requestData);
      message.success('Lu thnh cng');
    } catch (err) {
      message.error('C li xy ra');
    }
    this.reloadTableData();
    this.setState({ loadingLocking: false });
  }

  lockUnlockByRow = async data => {
    const { paramSearch: { programDetailId } } = this.state;
    try {
      await lockOrUnlockClassGradePartGrading([
        {
          classId: data.classId,
          studentId: data.studentId,
          programDetailId,
          isLocked: !data.isLocked,
        },
      ]);
      message.success('Lu thnh cng');
      this.reloadTableData();
    } catch (err) {
      message.error('C li xy ra');
      this.reloadTableData();
    }
  }

  reloadTableData = () => {
    this.setState({ reload: this.state.reload + 1 });
  }
  
  toggleGradePartPopup = visible => {
    this.setState({ visible });
  }

  trackSavingRecord = savingEntities => {
    this.setState({ savingEntities });
  }

  toggleDisableActionBtn = isDisableActionBtn => {
    this.setState({ isDisableActionBtn })
  }

  toggleDisableExportExcelBtn = isDisableExportBtn => {
    this.setState({ isDisableExportBtn });
  }

  trackSelectedRecord = selectedEntities => {
    this.setState({ selectedEntities });
  }

  render() {
    const {
      visible,
      selectedGradeParts,
      tableData,
      reload,
      paramSearch,
      isDisableActionBtn,
      isDisableExportBtn,
      loadingSaving,
      loadingLocking,
      loadingExporting
    } = this.state;
    return (
      <Card title="Danh sch nhp im lp qu trnh tn ch">
        <CartHeadAction>
          <BtnSave
            title="Lu"
            onClick={this.handleSave}
            disabled={isDisableActionBtn}
            loading={loadingSaving}
          />
          <Btn
            title="Kha im"
            disabled={isDisableActionBtn}
            onClick={this.lockGradeParts}
            loading={loadingLocking}
          />
          <Btn title="im thnh phn" onClick={() => this.toggleGradePartPopup(true)} />
          <BtnExport
            onClick={this.handleExportExcel}
            disabled={isDisableExportBtn}
            loading={loadingExporting}
          />
        </CartHeadAction>
        <Row span={24}>
          <FormInfoCommon
            selectedGradeParts={selectedGradeParts}
            handleSearch={this.handleSearch}
          />
        </Row>
        <Divider>Danh sch lp tn ch</Divider>
        <Row>
          <Col span={24}>
            <TableEnteringProcessScore
              data={tableData}
              paramSearch={paramSearch}
              reload={reload}
              reloadTableData={this.reloadTableData}
              trackSavingRecord={this.trackSavingRecord}
              trackSelectedRecord={this.trackSelectedRecord}
              toggleDisableActionBtn={this.toggleDisableActionBtn}
              toggleDisableExportExcelBtn={this.toggleDisableExportExcelBtn}
              lockUnlockByRow={this.lockUnlockByRow}
            />
          </Col>
        </Row>
        <GradePartPopup
          visible={visible}
          toggleGradePartPopup={this.toggleGradePartPopup}
          setSelectedGradeParts={this.setSelectedGradeParts}
        />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <EnteringProcessScorePage />
  </Page>
);



