import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getFacultyByRegisterSession = ({
  registerSessionId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/GradePartGrading/FacultyByRegisterSession',
    method: 'GET',
    params: {
      registerSessionId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createGradePartGrading = data =>
  request({
    prefix,
    url: '/GradePartGrading',
    method: 'POST',
    params: {},
    data,
  });

export const exportExcel = id =>
  requestDownload({
    prefix,
    defaultFileName: 'im qu trnh.pdf',
    url: '/GradePartGrading/ExportStudentGrade',
    method: 'POST',
    data: { id },
  });

export const lockOrUnlockGradePartGrading = data =>
  request({
    prefix,
    url: '/GradePartGrading',
    method: 'PUT',
    params: {},
    data,
  });



