import React, { Component } from 'react';
import { Switch, Form, InputNumber, Tooltip } from 'antd';
import { isEmpty } from 'lodash';
import { TableDataEditCell } from 'components/common';
import { getClassGradePartGradings } from 'services/classGradePartGrading';

class TableEnteringProcessScore extends Component {
  constructor(props) {
    super(props);
    this.state = {
      editedGradePartCredits: {},
      selectedRowKeys: [],
    };
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    const { editedGradePartCredits } = this.state;
    const savingEntites = Object.entries(editedGradePartCredits)
      .filter(keyValue => selectedRowKeys.includes(keyValue[0]))
      .map(keyValue => keyValue[1]);
    this.setState({ selectedRowKeys });
    this.props.trackSavingRecord(savingEntites);
    this.props.trackSelectedRecord(selectedRows);
    this.props.toggleDisableActionBtn(isEmpty(selectedRowKeys));
  };

  parseGradePartTableData = rawData =>
    rawData.map(item => {
      item.gradeParts.forEach(part => {
        item[`gradePart_${part.gradePartId}`] = part.grade;
        item.key = this.genGradePartKey(item);
      });
      return item;
    });

  handleSaveCellData = async rowData => {
    this.setState({
      editedGradePartCredits: {
        ...this.state.editedGradePartCredits,
        [this.genGradePartKey(rowData)]: rowData,
      },
    });
  };

  genGradePartKey = row => `${row.classCreditId}_${row.studentId}`;

  handleLockOrUnlock = data => {
    this.props.lockUnlockByRow(data);
  };

  renderChildRow = () => {
    const { data } = this.state;
    if (!isEmpty(data)) {
      const { gradeParts } = data[0];
      return gradeParts.map((part, index) => ({
        title: part.gradePartLabel,
        dataIndex: `gradePart_${index + 1}`,
        editable: true,
        alwayShowEdit: true,
        width: '100px',
        renderFormItemEdit: ({ form, record, onSave }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.key}.gradePart_${part.gradePartId}`, {
              initialValue: record[`gradePart_${part.gradePartId}`],
            })(
              <InputNumber
                onPressEnter={onSave}
                onBlur={onSave}
                disabled={record.isLocked}
                max={10}
                min={0}
              />
            )}
          </Form.Item>
        ),
      }));
    }
    return [];
  };

  render() {
    const columns = [
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'S tit ngh',
        dataIndex: 'noOfLeave',
        key: 'noOfLeave',
        width: '100px',
        align: 'center',
      },
      {
        title: 'im thnh phn',
        editable: true,
        width: '100px',
        children: this.renderChildRow(),
      },
      {
        title: 'Thao tc',
        dataIndex: 'isLocked',
        render: (value, record) => (
          <div style={{ textAlign: 'center' }}>
            <Tooltip placement="top" title={value ? 'M kha' : 'Kha'}>
              <Switch
                defaultChecked={!value}
                checked={!value}
                onChange={() => this.handleLockOrUnlock(record)}
              />
            </Tooltip>
          </div>
        ),
      },
    ];
    const { selectedRowKeys } = this.state;
    const { paramSearch, reload } = this.props;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <TableDataEditCell
          reload={reload}
          paramSearch={paramSearch}
          service={async params => {
            this.setState({ selectedRowKeys: [] });
            this.props.toggleDisableActionBtn(true);
            const result = await getClassGradePartGradings(params);
            if (result.data && result.data.items) {
              this.setState({ data: this.parseGradePartTableData(result.data.items) });
              this.props.toggleDisableExportExcelBtn(isEmpty(result.data.items));
            }
            return result;
          }}
          columns={columns}
          handleSaveCellData={this.handleSaveCellData}
          rowSelection={rowSelection}
          rowKey="key"
        />
      </div>
    );
  }
}

export default TableEnteringProcessScore;



