import React, { Component } from 'react';
import { Row, Form, Col } from 'antd';
// import { getTrainTypes } from 'services/trainType';
import { SelectAjax, YearSemesterInput } from 'components/common';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { requiredRule } from 'utils/validateRules';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getResgisterSessionSelect } from 'services/registerSession';
// import { getFacultyByRegisterSession } from 'services/gradePartGrading';
import { getSubjectByProgramDetail } from 'services/programDefinition';
// import { getClassCreditForSelect } from 'services/classCreditSchedule';

import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};

  componentWillReceiveProps(nextProps) {
    if (nextProps.reload !== this.props.reload) {
      this.handleSearch(null, nextProps.selectedGradeParts);
    }
  }

  getResgisterSessionSelectCustom = async params => {
    const res = await getResgisterSessionSelect({ ...params, fullFormat: true });
    return res;
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = (e) => {
    if (e) {
      e.preventDefault();
    }
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        this.props.handleSearch(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const classId = getFieldValue('classId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
      classId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn lp',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn lp!',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn nm hc!',
          },
        ],
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} span={5} />
          <Col span={5}>
            <Form.Item label="Hc phn">
              {getFieldDecorator('programDetailId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  keyValue="value"
                  // keyLabel="subjectLabel"
                  service={getSubjectByProgramDetail}
                  allowGetData={!!classId && !!yearEduId && !!semesterId}
                  params={{ classId, yearEduId, semesterId }}
                  form={this.props.form}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



