import { request, requestDownload } from 'utils';
import qs from 'qs';

const prefix = '/api/education';

export const getClassGradePartGradings = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  programDetailId,
  gradeParts,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassGradePartGrading',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      programDetailId,
      gradeParts,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    paramsSerializer: params => qs.stringify(params, { arrayFormat: 'repeat' }),
    data: {}
  });

export const createClassGradePartGrading = data =>
  request({
    prefix,
    url: '/ClassGradePartGrading',
    method: 'POST',
    params: {},
    data
  });


export const lockOrUnlockClassGradePartGrading = data =>
  request({
    prefix,
    url: '/ClassGradePartGrading',
    method: 'PUT',
    params: {},
    data
  });

export const exportClassGradePartGrading = ({ classId, programDetailId } = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'im qu trnh lp HC.pdf',
    url: '/ClassGradePartGrading/ExportClassGradePartGrading',
    method: 'POST',
    data: { classId, programDetailId }
  });


